

html {
  font-size: 16px;
}


body {
  overflow-y: scroll;
  overflow-x: hidden;
  user-select: none;
}

#HomeVideo {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
}

.nav-darker .timeline-block p {
  color: #ABABAB;
}

.nav-darker #HomeMenuItem {
  opacity: 0.5;
}

.nav-darker .step {
  background-color: #ABABAB;
  opacity: 1;
}

.container {
  background-color: gray;
  overflow-y: scroll;
}

.roboto-regular {
  font-family: 'Roboto';
  font-weight: 300;
}

.roboto-medium {
  font-family: 'Roboto';
  font-weight: 400;
}

.roboto-bold {
  font-family: 'Roboto';
  font-weight: 500;
}

img {
  max-width: 100%;
}

p {
  margin: 0 !important;
}

#Test1,
#Test2,
#Test3,
#Test4 {
  width: 20px;
  height: 20px;
  border: 2px solid white;
  background: #49BAF1;
  border-radius: 24px;
  margin: 16px;
  opacity: 1;
}

#Test1.active,
#Test2.active,
#Test3.active,
#Test4.active {
  background: white;
}

.panel {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

/* section:not(.first) {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.8);
  } 
   */

section {
  height: 500vh;
  position: relative;
}

.pin {
  position: relative;
  height: 100vh;
}

.block {
  position: absolute;
  top: 0;
  left: 0;
}

.block img {
  /* max-height: 100vh; */
}


.values .title {
  margin-top: 2.5rem;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  color: #0F3A57;
  line-height: 2.5rem;
}

.values .v-item {
  margin-right: 9rem;
  margin-bottom: 9rem;
  padding: 0;
}

.values p {
  font-size: 1rem;
  color: #0F3A5766;
}

.meir-img img {
  max-width: unset;
}

.timeline-container {
  display: flex;
  flex-direction: column;
}

.timeline-block {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.timeline-block p{
  margin-right: 32px !important;
  margin-bottom: 0px !important;
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 13px;
  cursor: pointer;
  color: #F4F7F8;
  user-select: none;
}

.step {
  width: 16px;
  height: 3px;
  background-color: #F4F7F8;
  margin-top: 8px;
  opacity: 0.3;
}

.active {
  opacity: 0.3;
}


@media only screen and (max-width: 2472px) {
  
  #GREATMIND {
    height: 110vw;
  }
}

@media only screen and (min-width: 2472px) {
  .meir-img {
    left: 0 !important;
  }
  .meir-img img {
    width: 100vw;
  }
  #GREATMIND {
    height: 80vw;
  }
  .great-mind {
    right: 0 !important;
  }
  .great-mind img{
    width: unset;
  }
}

@media only screen and (max-width: 1720px) {
  .meir-img {
    left: -3vw !important;
  }
  .meir-img img {
    width: 130vw;
  }
  
  #GREATMIND {
    height: 130vw;
  }
  .great-mind {
    right: -30vw !important;
  }
}

@media only screen and (min-width: 1600px) {

  .popout-1-div {
    bottom:36%;
    left:20%;
  }
  .popout-1-div h4 {
    font-size: 1.5rem;
  }
  .popout-1-div h5{
    font-size: 1rem;
  }
  .popout-2-div h4 {
    font-size: 1.5rem;
  }
  .popout-2-div h5{
    font-size: 1rem;
  }
  .popout-3-div h4 {
    font-size: 1.5rem;
  }
  .popout-3-div h5{
    font-size: 1rem;
  }
  
  .popout-2-div {
    bottom:20%;
    left:12%;
  }
  .popout-3-div {
    bottom:12%;
    left:55%;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1600px) {

  .popout-1-div {
    bottom:32%;
    left:10%;
  }
  .popout-2-div {
    flex-direction: row-reverse;
  }
  .popout-2-div {
    bottom:17%;
    left:10%;
  }
  .popout-3-div {
    bottom:2%;
    left:10%;
  }
  .popout-3-div {
    flex-direction: row-reverse;
  }

  .popout-1-div h4 {
    font-size: 1rem;
  }
  .popout-1-div h5{
    font-size: 0.75rem;
  }
  .popout-2-div h4 {
    font-size: 1rem;
  }
  .popout-2-div h5{
    font-size: 0.75rem;
  }
  .popout-3-div h4 {
    font-size: 1rem;
  }
  .popout-3-div h5{
    font-size: 0.75rem;
  }
}

